body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  background-color: #222D47;
  color: #242830;
}
.projectCard {
    margin: 10px 0 10px 0;
    width: 16rem;
    background-color: #CED2DD;
}

.projectImageWrapper {
    padding: 20px 10px 5px 10px;
    background-color: #CED2DD;
}

.projectBody {
    background-color: #CED2DD;
}

.projectTitle {
    font-weight: bold;
    font-size: larger;
}

.projectTech {
    font-style: italic;
    font-weight: lighter;
    font-size: smaller;
    padding-bottom: 10px;
}

.projectText {
    font-size: smaller;
    margin-bottom: 45px;
}

.projectLinksArea {
    position: absolute;
    bottom: 0;
}

.codeLink {
    color: black;
    font-weight: lighter;
    font-size: smaller;
}

.codeLink:hover {
    color: #203C82;
    font-weight: bolder;
    text-decoration: none;
}

@media only screen and (min-width: 1200px) {
    .projectCard {
        width: 16rem;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .projectCard {
        width: 100%;
    }
}

@media only screen and (max-width: 767px) {
    .projectCard {
        width: 100%;
    }
}
.portfolioArea {
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, auto));
}

.portfolioItem {
    float: left;
    margin: 0 15px;
}

.bioCard {
    margin: 10px;
    width: 15rem;
    background-color: #CED2DD;
}

.bioImage {
    padding: 20px 10px 5px 10px;
    background-color: #CED2DD;
}

.bioBody {
    background-color: #CED2DD;
}

.bioTitle {
    font-weight: bold;
    font-size: large;
}

.bioContent {
    font-style: italic;
    font-weight: lighter;
    font-size: smaller;
}

.bioText {
    padding: 10px 0 0 0;
}

.bio {
    font-size: smaller;
}

.skills {
    font-size: smaller;
}

.socialLink {
    color: black;
    font-weight: lighter;
    font-size: smaller;
}

.socialLink:hover {
    color: #203C82;
    font-weight: bolder;
    text-decoration: none;
}

.socialIcon {
    height: 17px;
}

@media only screen and (min-width: 1200px) {
    .bioCard {
        width: 15rem;
    }

    .portfolioArea {
        margin: 0;
        display: grid;
        grid-template-columns: 17rem 17rem 17rem;
    }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
    .bioCard {
        width: 17rem;
    }

    .portfolioArea {
        margin: 0;
        display: grid;
        grid-template-columns: 17rem 17rem;
    }

    .portfolioItem {
        margin: 0 25px;
    }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
    .bioCard {
        width: 18rem;
        position: fixed;
        top: 0;
    }

    .portfolioArea {
        margin: 0;
    }
}

@media only screen and (max-width: 767px) {
    .bioCard {
        margin: 10px 0;
        width: 100%;

        text-align: center;
    }

    .bioLinksArea {
        display: flex;
        justify-content: space-between;
    }
}
